
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: "montseratt", sans-serif;
}

.app {
    /* background-image: url("./photos/coldweather.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: bottom;
    transition: 0.4 ease; */

    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    /* Image by M. Maggs from Pixabay */
    background-image: url("./photos/coldweather.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    transition: 0.4 ease;
}

.app.warm {
    /* Image by M. Maggs from Pixabay */
    background-image: url("./photos/warmweather.png");
}

main {
    min-height: 100vh;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.75));
    padding: 25px;
}

.search-box {
    width: 100%;
    margin: 0 0 0px;
}

#name {
    margin: 0 0 75px;
    padding: 10px;
    text-align: right;
    color: #FFF;
    font-weight: 700;
}

.search-box .search-bar {
    display: block;
    width: 100%;
    padding: 15px;

    appearance: none;
    background: none;
    border: none;
    outline: none;

    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 0px 0px 16px 16px;
    margin-top: -25px;

    box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

    color: #313131;
    font-size: 20px;

    transition: 0.4s ease;
}

.search-box .search-bar:focus {
    background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
    color: #FFF;
    font-size: 32px;
    font-weight: 500;
    text-align: left;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
    color: #FFF;
    font-size: 20px;
    font-weight: 300px;
    font-style: italic;
    text-align: left;
    text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
    text-align: center;
}

.weather-box .temp {
    /* position: relative;
    display: inline-block;
    margin: 30px auto;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 16px;

    padding: 15px 25px; */

    color: #FFF;
    font-size: 102px;
    font-weight: 500;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);

    /* text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
    text-align: center;
    box-shadow: 3px 6px rgba(0, 0, 0, 0.2); */
}

.weather-box .weather {
    color: #FFF;
    font-size: 48px;
    font-weight: 500;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.weatherInfo-box {
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    padding: 25px 35px;

    color: #FFF;
    font-size: 23px;
    font-weight: 500;

    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.weatherInfo {
    padding: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

